import { Component, AfterContentInit } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Store } from '@ngrx/store';
import { TalkState } from '../../app/ng/reducers/states';

import * as TalkReducer from '../../app/ng/reducers/talk.reducer';
import { Observable } from 'rxjs/Observable';
import { TalkInfo } from '../../app/ng/models/srtModel';

import {Clipboard} from 'ts-clipboard';



@Component({
  selector: 'page-list',
  templateUrl: 'list.html'
})
export class ListPage implements AfterContentInit {
 
  talks$: Observable<TalkInfo>;

  talkInfos = [];

  constructor(public navCtrl: NavController, private talkStore: Store<TalkState>) {
   
    this.talks$ = talkStore.select(TalkReducer.getTalk);
  }

  ngAfterContentInit() {

    this.talks$.subscribe(
      (talkInfo: TalkInfo) => {
        this.talkInfos.push(talkInfo);
        console.log(talkInfo.talkList[0]);
      }
    );
  }

  ccopy() {
    

    let copyData = '';

    for (const talkInfo of this.talkInfos) {
      for(const it of talkInfo.talkList) {

        copyData += it[1].op[1].author;
        copyData += ' - ';
        copyData += '<a href="https://steemit.com/' + it[1].op[1].json_metadata.tags[0] + '/@' + it[1].op[1].parent_author + '/' + it[1].op[1].parent_permlink + '"' + ' target="_blank">' +it[1].timestamp + '</a>';
        copyData += '<br/>';
        copyData += it[1].op[1].body;
        copyData += '<br/>';
      }
    }

    Clipboard.copy(copyData);

  }

}




