import { Component } from '@angular/core';
import { NavController, LoadingController, Loading } from 'ionic-angular';

import * as steem from "steem";
import { Store } from '@ngrx/store';
import { ModeState, TalkState } from '../../app/ng/reducers/states';

import * as ModeReducer from '../../app/ng/reducers/mode.reducer';
import * as TalkReducer from '../../app/ng/reducers/talk.reducer';
import * as fromActions from '../../app/ng/actions/actions';
import * as srtFromActions from '../../app/ng/actions/srtActions';
import { TalkInfo } from '../../app/ng/models/srtModel';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  youId: string;
  friendId: string;

  

  modes$ = null;
  // talks$ = null;

  loader: Loading;

  constructor(public navCtrl: NavController,
    private modeStore: Store<ModeState>,
    private talkStore: Store<TalkState>,
    public loadingCtrl: LoadingController) {

      this.modes$ = modeStore.select(ModeReducer.getMode);
    // this.talks$ = talkStore.select(TalkReducer.getTalk);
  }


  search() {

    console.log(this.youId);
    console.log(this.friendId);

    this.getYouInfo(this.youId);
  }

  getYouInfo(youId: string) {

    this.loader = this.loadingCtrl.create({
      content: "Please wait...",
      // duration: 3000
    });

    this.loader.present();

    const _this = this;
    steem.api.getAccountHistory(youId,-1,10000,(err, result) => {

      if (err != null) {
        alert('에러났어요');
        return;
      }

      _this.parseTalk(result);

    });
  }

  parseTalk(result) {


    const list = result;

    const realList = new Array();
    const talkList: Array<any> = new Array();
    for(let i = 0 ; i < list.length; ++i) {
        const data = list[i];

        // console.log(data[0]);
        
        if(data[1].op[0] == undefined || data[1].op[0] == null || data[1].op[0] != 'comment') {
            continue;
        }

        const cmtData = data[1].op[1];

        if(data[1].op[1].parent_author == undefined
             || data[1].op[1].parent_author == null
             || data[1].op[1].parent_author == ''
            ) {
            continue;
        }

        
        if(cmtData.parent_author == this.friendId || cmtData.author == this.friendId) {

            // ddd += cmtData.author;
            // ddd += '\r\n';
            // ddd += cmtData.body;
            // ddd += '\r\n';
            // json_metadata
            data[1].op[1].json_metadata = JSON.parse(data[1].op[1].json_metadata);
            talkList.push(data);
            
        }
        
    }

    // this.viewTalkList = talkList;

    const ti: TalkInfo = {
      talkList : talkList,
      youId : this.youId,
      friendId : this.friendId
    }

    this.loader.dismissAll();

    this.talkStore.dispatch(new srtFromActions.TalkAction(ti));

    this.modeStore.dispatch(new fromActions.ModeAction('talk'));

    

  }

}



